/*
Font
font-family: $font-family;

Light
font-weight: 300;

Regular
font-weight: 400;

Semibold
font-weight: 600;

Bold
font-weight: 700;
*/

@import "_variables.scss";
@import "_slick.scss";
@import "_base.scss";

img.logo {
  width: 131px;
  height: 38px;
}

.header {
  padding-top: 10px;
  padding-bottom: 25px;
}

.header a {
  font-family: $font-family;
  font-weight: 600;
  font-size: 16px;
  color: $turquoise;
  letter-spacing: 0.22px;
  line-height: 26px;
  text-decoration: none;
  padding: 10px 50px;
}

.section-1 {
  background-color: $athens-gray;
  height: 800px;
}

.section-2, .section-3 {
  width: 100%;
  margin-top: 180px;
}

.section-1-image {
  width: 1100px;
  height: 800px;
  background-color: $athens-gray;
  position: absolute;
  margin-left: 580px;
  background-image: url('../img/the-best-healthcare-education-in-the-world.jpg');
  background-size: contain;
  background-repeat: no-repeat;
}

.section-1-blanktop {
  position: absolute;
  width: 580px;
  height: 53px;
  background-color: $white;
}

.section-1-arrowdown {
  margin-left: 650px;
  margin-top: -20px;
  position: absolute;
  cursor: pointer;
}

.section-1 .text-container {
  width: 440px;
  padding-left: 100px;
  padding-top: 450px;
}

.svg-best-education {
  position: absolute;
  z-index: 9998;
  padding-top: 180px;
  padding-left: 85px;
}

.section-2-image {
  width: 775px;
  height: 840px;
  background-image: url('../img/anywhere.jpg');
  background-size: contain;
  background-repeat: no-repeat;
  float: right;
}

.section-4 {
  width: auto;
}

img.always-precise, img.always-anywhere, img.always-effective {
  width: 473px;
  height: auto;
}

.section-4-image {
  background-image: url('../img/always-effective-image-2.png');
  width: 775px;
  height: 775px;
  background-size: contain;
  background-repeat: no-repeat;
  float: right;
}

.section-2 .text-container {
  padding-left: 0;
  padding-top: 530px;
  width: 350px;
}

img.always-anywhere {
  position: absolute;
  padding-top: 300px;
}

img.always-effective {
  position: absolute;
  padding-top: 260px;
}

.section-3 {
  margin-top: 180px;
}

.section-3-image {
  width: 775px;
  height: 775px;
  background-color: $athens-gray;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url('../img/always-precise.jpg');
  float: left;
}

img.always-precise {
  position: absolute;
  margin-left: -120px;
  margin-top: 230px;
}

.section-3 .text-container {
  padding-left: 0;
  padding-top: 460px;
  width: 350px;
  float: right;
}

.fullwidth-container-grey {
  margin-top: 180px;
  width: 100%;
  background-color: $athens-gray;
  padding-bottom: 30px;
}

.section-courses {
  padding-top: 108px;
}

.section-courses-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.section-courses-flex-holder {
  order: 1;
  width: 300px;
}

.section-courses-info {
  margin-top:90px;
  margin-left:50px;
  margin-right:30px;
  float:left;
}

.section-courses-header {
  width:100px;
  float:left;
}

.section-courses h1 {
  letter-spacing: 0.84px;
  line-height: 46px;
  margin-left: 0;
}

.section-courses span.info {
  font-family: $font-family;
  font-weight: 600;
  font-size: 20px;
  color: $smalt;
  letter-spacing: 0;
  line-height: 40px;
  border-top: 2px solid $smalt;
  padding-top:20px;
}

.section-courses-text {
  width:475px;
  order:3;
  font-family: $font-family;
  font-weight: 300;
  color: $downriver;
  letter-spacing: 0.32px;
  margin-left: 20px;
  font-size: 28px;
  line-height: 36px;
  font-style: italic;
}

.section-courses-samples {
  order:4;
  width:148px;
  text-align:right;
  color: $vivid-tangerine;
  font-family: $font-family;
  font-weight: 400;
  font-size: 19px;
  letter-spacing: 0;
  line-height: 28px;
}

.section-courses-samples span {
  background: $vivid-tangerine;
  padding:3px 10px 3px 10px;
  color: $white;
}

.course-scroll-hold {
  position: absolute;
  width: 200%;
  height: 620px;
  overflow-y: hidden;
}

.courses {
  padding-top:69px;
  width: 6000px;
  height: 550px;
  max-width: 100%;
  position: relative;
}

.courses-separator {
  clear: none;
}

.courses .single-course {
  width: 269px;
  height: 240px;
  background-position: 10px 0;
  background-repeat: no-repeat;
  background-color: $white;
  float:left;
  margin-right: 25px;
  overflow: hidden;
  margin-bottom: 43px;
}

.previewable-course:hover {
  box-shadow: 0 6px 12px 0 rgba(0,0,0,0.19);
  cursor: pointer;
}

.single-course-1 {
  background: url('../img/course-1.jpg');
  background-size: contain;
}

.single-course-2 {
  background: url('../img/course-2.jpg');
  background-size: contain;
}

.single-course-3 {
  background: url('../img/course-3.jpg');
  background-size: contain;
}

.single-course-4 {
  background: url('../img/course-4.jpg');
  background-size: contain;
}

.single-course-5 {
  background: url('../img/course-5.jpg');
  background-size: contain;
}

.single-course-6 {
  background: url('../img/course-6.jpg');
  background-size: contain;
}

.courses .single-course .sample-sign {
  position:absolute;
  background: $vivid-tangerine;
  color:$white;
  margin-left:-10px;
  position:absolute;
  font-family: $font-family;
  font-weight: 700;
  font-size: 13px;
  letter-spacing: 0.1px;
  line-height: 13px;
  padding: 15px 20px 15px 30px;
  margin-top:107px;
}

.courses .single-course .lesson-text-holder {
  top: 177px;
  width: 275px;
  height: 70px;
  text-align: center;
  position: relative;
  margin-left: 10px;
  line-height: 50px;
}

.courses .single-course .lesson-text-holder .title {
  font-family: $font-family;
  font-weight: 400;
  font-size: 20px;
  color: $smalt;
  letter-spacing: 0;
  width: 200px;
  margin: auto;
  margin-right: 47px;
  vertical-align: middle;
  display: inline-block;
  line-height: 20px;
}

.courses .single-course .lesson-text-holder .lessons {
  opacity: 0.4;
  font-family: $font-family;
  font-weight: 300;
  font-size: 16px;
  color: $downriver;
  letter-spacing: 0.27px;
  line-height: 26px;
  /* temporary */
  display: none;
}

.absolute-courses-holder {
  position: absolute;
  margin-right: -2200px;
  max-height: 550px;
  overflow: hidden;
}

#References {
  padding: 100px 0 100px;
}

#References h1 {
  letter-spacing: 1.12px;
  line-height: 48px;
  width: 260px;
  text-align: right;
  margin-left: 60px;
  float:left;
}

#References .flex-holder {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-bottom: 20px;
}

#References .flex-header {
  flex: 1;
  margin-left: 60px;
}

#References .flex-content {
  flex: 1;
  margin-right: 60px;
}

.header-line {
  position:absolute;
  background: $smalt;
  width:75px;
  height:2px;
  float:left;
  margin-top:75px;
}

.reference-square {
  width: 280px;
  height:232px;
  box-shadow: 0 13px 25px 0 rgba(0,12,37,0.12);
  position: absolute;
  font-family: $font-family;
  font-weight: 400;
  font-size: 18px;
  color: $white;
  letter-spacing: 3px;
  text-transform: uppercase;
  z-index: 2;
}

.reference-square-red {
  background: $vivid-tangerine;
  margin-left: 117px;
}

.reference-square-white {
  background: $white;
  margin-top: 180px;
  margin-left: 368px;
  z-index: 9998;
}

.reference-square-blue {
  background: $downriver;
  margin-top: 50px;
  margin-left: 610px;
}

.references-holder {
  position: absolute;
  width: 950px;
  height: 510px;
  left: 50%;
  margin-left: -475px;
}

.references-bg-1 {
  position: absolute;
  width: 164px;
  height: 145px;
  margin: -40px 22px;
}

.references-bg-2 {
  position: absolute;
  margin-left: 724px;
  margin-top: 232px;
  width: 164px;
  height: 141px;
}

.reference-square div {
  padding: 14% 0;
  width: 90%;
  text-align: center;
  margin: auto;
}

.reference-square div img {
  width: 50px;
  height: auto;
}

svg {
  height: 50px;
  height: 50px;
}

svg.white path {
  fill: $white !important;
}

svg.blue path {
  fill: $downriver !important;
}

.reference-square-red div img {
  padding-top: 11%;
}

.reference-square-white div img {
  padding-top: 15%;
}

.reference-square-blue div img {
  padding-top: 20%;
}

.references-center-line {
  width: 1px;
  height: 547px;
  opacity: 0.1;
  background: $downriver;
  margin-left: 50%;
  margin-top: -40px;
  z-index: 1;
}

.references-center-line-2 {
  display: none;
}

#References h4 {
  margin: auto;
  width: 268px;
  font-family: $font-family;
  font-weight: 300;
  font-size: 30px;
  color: $downriver;
  letter-spacing: 0.6px;
  line-height: 28px;
  text-align: center;
}

.references-separator {
  margin: auto;
  width: 80px;
  height: 2px;
  background-color: $downriver;
  margin-top: 20px;
  margin-bottom:20px;
}

#Footer {
  margin-top:120px;
  font-size: 15px;
}

#Footer .flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#Footer .top-footer {
  padding: 50px 20px 50px 20px;
  border-bottom: 1px e6e8ec solid;
}

#Footer .bottom-footer {
  font-size: 14px;
  padding: 20px 20px 30px 20px;
}

#Footer .top-footer div, Footer .bottom-footer div {
  flex:1;
}

#Footer .bottom-footer a.language {
  font-weight: 600;
  font-size: 16px;
}

#Footer .top-login {
  text-align: center;
}

#Footer .top-invoicing {
  text-align: right;
  line-height: 20px;
}

#Footer .bottom-footer-terms {
  order: 1;
}

#Footer .bottom-footer-inc {
  text-align: center;
  order: 2;
}

#Footer .bottom-footer-language {
  text-align: right;
  order: 3;
}

.arrow-holder {
  height: 40px;
  margin-left: 30px;
  padding-top: 636px;
}

.arrow-holder img {
  float:left;
  cursor: pointer;
}

.section-staff {
  z-index: 9998;
  padding-top: 380px;
  min-height: 260px;
}

.staff-scroll-holder {
  z-index: 9998;
  width: 65%;
  margin-left: 35%;
  margin-right: auto;
  height: 370px;
  position: absolute;
  overflow-y: hidden;
  /*overflow-x: hidden;*/
}

.staff-holder {
  position:absolute;
  width: 6140px;
  height: 370px;
}

.staff-image {
  position: relative;
  width: 370px;
  height: 370px;
  background-size: cover;
  background-color: $vivid-tangerine;
  background-blend-mode: multiply;
  float:left;
  opacity: .66;
  margin-right: -1px;
}

.staff-desc {
  position: absolute;
  padding: 292px 30px;
  font-family: $font-family;
  font-weight: 700;
  font-size: 18px;
  color: $white;
  letter-spacing: 0.3px;
  line-height: 23px;
}

.staff-desc a, .staff-desc a:link, .staff-desc a:hover, .staff-desc a:active {
  font-family: $font-family;
  font-weight: 700;
  font-size: 18px;
  color: $white;
  text-decoration: none;
}

.staff-image-overlay {
  position: absolute;
  height: 370px;
  width: 6140px;
  background-color: $vivid-tangerine;
  opacity: .99;
  height: 1109px;
}

.contacts-textcontainer {
  margin-left: 50%;
  float: left;
  padding: 50px 200px;
  width: 375px;
  text-align: right;
}

.section-contacts .header-line {
  margin-left:70px;
  background:$vivid-tangerine;
}

.section-contacts h1 {
  color: $vivid-tangerine;
  width: 220px;
  float: right;
  letter-spacing: 1.12px;
  line-height: 48px;
}

.section-contacts span {
  float:right;
  padding-top:49px;
}

.section-contacts .map-holder {
  z-index:9999;
  position:absolute;
  width:50%;
  margin-right: auto;
  margin-left: auto;
  background-color: $athens-gray;
  height: 385px;
  text-align:right;
}

.section-contacts .map {
  margin-top:62px;
  width:676px;
  height:362px;
  margin-right: -101px;
}

.section-staff .contact-details {
  padding: 80px 0;
  width: 270px;
}

.login-modal {
  display: none;
  z-index: 99999999;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.login-holder {
  position:absolute;
  margin:auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 575px;
  height: 530px;
  background: $turquoise;
  box-shadow: 0 23px 53px 0 rgba(0,11,36,0.15);
  color: $white;
  text-align:center;
}

.login-modal h1 {
  margin-top:72px;
  color:$white;
}

.login-text {
  text-align: left;
  padding: 40px 80px 20px 80px;
}

.login-text label {
  text-transform: uppercase;
  opacity: 0.5;
  font-family: $font-family;
  font-weight: 700;
  font-size: 13px;
  letter-spacing: 1.23px;
  line-height: 28px;
}

.login-text input {
  width: 370px;
  height: 48px;
  font-size: 18px;
  padding: 0 20px 0 20px;
}

.login-modal button {
  width: 100%;
  border: 0;
  text-align: center;
  margin-top: 40px;
  padding: 10px;
  background: $smalt;
  color: $white;
  font-family: $font-family;
  font-weight: 600;
  font-size: 19px;
  letter-spacing: 0.34px;
  line-height: 26px;
}

.login-modal .facebook {
  margin-top: 28px;
  color: $smalt;
  letter-spacing: 0.34px;
  line-height: 26px;
  text-align: center;
  width: 100%;
}

.login-modal a {
  font-family: $font-family;
  font-weight: 600;
  font-size: 19px;
}

.close {
  width: 56px;
  height: 56px;
  background: $smalt;
  position: absolute;
  overflow: hidden;
  cursor: pointer;
  text-align: center;
}

.login-holder .close {
  margin-left: 532px;
  margin-top: -12px;
}

.staff-arrows {
  width: 65%;
  margin-left: 35%;
  margin-right: auto;
  position: absolute;
  z-index: 9998;
}

.staff-arrows-holder {
  width: 88px;
  height: 48px;
  background-color: $vivid-tangerine;
  position: absolute;
  margin-top: 370px;
  overflow: hidden;
}

.staff-arrows-holder div {
  padding:10px;
  text-align:center;
}

.arrow-left, .arrow-right {
  cursor: pointer;
}

.general-modal {
  display:none;
  z-index: 99999999;
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.terms-holder, .descfile-holder {
  position:absolute;
  margin:auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 90%;
  height: 90%;
  background: $turquoise;
  box-shadow: 0 23px 53px 0 rgba(0,11,36,0.15);
  text-align:center;
  overflow: hidden;
}

.terms-holder .padd, .descfile-holder .padd {
  padding: 30px 30px;
  overflow-x: hidden;
  overflow-y: auto;
  height: 720px;
}

.terms-holder h1, .descfileholder h1 {
  line-height: 56px;
}

descfile-modal .padd h1 {
  padding-top: 50px;
}

.descfileholder table {
  border: 0;
}

.video-holder {
  position:absolute;
  margin:auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 640px;
  height: 200px;
  background: $turquoise;
  box-shadow: 0 23px 53px 0 rgba(0,11,36,0.15);
  text-align:center;
}

.video-holder .close {
  margin-left: 597px;
  margin-top: -12px;
  z-index: 9999;
}


.logos-wrapper {
  height:100%;
  width: 100%;
  margin: 0;
  padding: 0;
  border: 0;
}

.logos-wrapper td {
  vertical-align: middle;
  text-align: center;
}

.logos-wrapper td img {
  width: 150px;
}

.logos-wrapper td img.turkuamk {
  margin-top: -26px;
}

.logos-wrapper td img.xamk {
  width: 116px;
}

.logos-wrapper td img.pkterveys {
  width: 80px;
}

.logos-wrapper td img.pdl {
  width: 90px;
  padding-top: 20px;
}

.header {
  a {
    float:right;
    &.lang {
      padding: 10px 0;
    }
  }
}


/* Small */
@media only screen
and (max-width: 767px) {

  .section-1 {
    height: auto;
    text-align: center;
  }

  .header {
    margin: 0 18px 0 18px;
    padding-bottom: 10px;
  }

  .section-1-blanktop, .header a.customerservice, .section-1-arrowdown {
    display: none;
  }

  .header a.lang {
    padding: 2px 25px;
  }

  .section-1-image {
    position: relative;
    margin-left: 0;
    width: 100%;
    background-color: $white;
    background-size: contain;
    height: 0;
    padding-top: 70%;
  }

  .svg-best-education {
    position: relative;
    width: 70%;
    margin: 0;
    padding: 30px 0;
  }

  .section-2, .section-3 {
    width: auto;
    margin-top: 100px;
  }

  .section-1 .text-container {
    position: relative;
    padding: 0 20px 30px 20px;
    width: 90%;
    margin: auto;
  }

  .section-2 {
    text-align: left;
    margin-left: 20px;
    margin-top: 67px;
  }

  .section-2-image {
    width: 100%;
    height: 0;
    padding-top: 109%;
  }

  .section-2 .text-container {
    padding-left: 0;
    padding-top: 20px;
    width: 80%;
  }

  .section-3 {
    float: none;
    margin-right: 20px;
  }

  .section-3-image {
    float: none;
    width: 114%;
    height: 0;
    padding-top: 100%;
  }

  .section-3 .text-container {
    float: right;
    padding-top: 30px;
    width: 80%;
  }

  img.always-precise {
    float: right;
    position: relative;
    margin-top: 30px;
    /* padding: 0; */
    width: 80%;
    height: auto;
  }

  img.always-anywhere, img.always-effective {
    position: relative;
    padding-top: 38px;
    padding-left: 0;
    width: 80%;
    height: auto;
  }

  button.primary {
    background: none;
    border: 1px solid $turquoise;
    color: $turquoise;
    padding: 1px 8px 1px 8px;
  }

  img.logo {
    width: 97px;
    height: 28px;
  }

  .header button.primary {
    margin-right: 0;
  }

  .fullwidth-container-grey {
    margin-top: 92px;
  }

  .courses {
    margin-left: 10px;
    height: 340px;
    max-width: 100%;
    position: relative;
  }

  .section-courses-samples {
    display: none;
  }

  .section-courses span.info {
    padding-top: 10px;
  }

  .section-courses-text {
    font-size: 18px;
    width: auto;
    margin-left: 0;
    margin-right: 20px;
    padding-top: 30px;
    clear: both;
  }

  .absolute-courses-holder {
    position: absolute;
    margin-right: -4000px;
    max-height: 260px;
    overflow: hidden;
  }

  .section-courses-flex {
    display: inline-block;
    margin-left: 20px;
  }

  .section-courses-info {
    float: right;
    margin-top: 57px;
  }

  .section-courses {
    padding-top: 36px;
  }

  .section-4 {
    width: 100%;
  }

  .section-4-image {
    background-image: url('../img/always-effective-image-2.png');
  }

  #References {
    padding-top: 40px;
  }

  #References h1 {
    margin-left: 0;
    text-align: left;
  }

  #References .flex-holder {
    flex-direction: column;
    margin: 0 20px 0 20px;
  }

  #References .flex-header {
    margin: 0 20px 20px 0;
  }

  #References .flex-content {
    margin-right: 0;
  }

  .header-line {
    margin-left: 161px;
  }

  .reference-square {
    width: 234px;
    height: 194px;
  }

  .reference-square-red {
    margin: 80px 20px;
  }

  .reference-square-white {
