body {
  overflow-x: hidden;
  max-width: 100%;
  width: 100%;
  font-family: $font-family;
  font-weight: 300;
  font-size: 19px;
  color: $downriver;
  letter-spacing: 0.32px;
  line-height: 28px;
  margin: 0;
  padding: 0;
  @media only screen
  and (max-width: 767px) {
    margin: 0;
    font-size: 19px;
  }
  @media only screen
  and (min-width: 768px)
  and (max-width: 1280px) {
    font-size: 18px;
    letter-spacing: 0.3px;
    line-height: 26px;
  }
}

.section, .header, .container {
  max-width: 100%;
  position: relative;
  overflow: hidden;
}

h1 {
  margin: 0;
  font-family: $font-family;
  font-weight: 400;
  font-size: 56px;
  color: $smalt;
  @media only screen
  and (max-width: 767px) {
    font-size: 40px;
  }
  @media only screen
  and (min-width: 768px)
  and (max-width: 1280px) {
    font-size: 42px;
  }
}

a {
  color: $smalt;
  text-decoration: none;
  font-family: $font-family;
  font-weight: 400;
}

button.primary {
  background: $turquoise;
  border: 2px solid $turquoise;
  font-family: $font-family;
  font-weight: 600;
  font-size: 16px;
  color: $white;
  letter-spacing: 0.29px;
  line-height: 26px;
  padding: 10px 20px 10px 20px;
  cursor: pointer;
}

.container {
  width: 1150px;
  margin-left: auto;
  margin-right: auto;
  display:block;
  @media only screen
  and (min-width: 768px)
  and (max-width: 1280px) {
    width: 100%;
  }
}
