.slider {
  width: 100%;
  margin: 0;
}

.slick-slide img {
  margin: auto;
  display: block;
}

.slick-slide {
  margin: 38px 20px;
  @media only screen
  and (max-width: 479px) {
    margin: 28px 20px;
  }
}

.slick-slide img {
  @media only screen
  and (max-width: 479px) {
    max-width: 78px;
  }
  @media only screen
  and (min-width: 480px) {
    max-width: 130px;
  }
  @media only screen
  and (min-width: 768px) {
    max-width: 150px;
  }
  @media only screen
  and (min-width: 1100px) {
    max-width: 160px;
  }
}

@media only screen
and (max-width: 479px) {
  #References section .slick-slide:nth-child(4), #References section .slick-slide:nth-child(5) {
    margin: 32px 20px;
  }
  #References section .slick-slide:nth-child(9) {
    margin: 14px 20px;
  }
  #References section .slick-slide:nth-child(10) {
    margin: 35px 20px;
  }
  #References section .slick-slide:nth-child(13), #References section .slick-slide:nth-child(14) {
    margin: 34px 20px;
  }
}

@media only screen
and (min-width: 480px)
and (max-width: 767px) {
  #References section .slick-slide:nth-child(9) {
    margin: 18px 20px;
  }
  #References section .slick-slide:nth-child(10) {
    margin: 56px 20px;
  }
  #References section .slick-slide:nth-child(11) {
    margin: 50px 20px;
  }
  #References section .slick-slide:nth-child(13), #References section .slick-slide:nth-child(14) {
    margin: 56px 20px;
  }
}

@media only screen
and (min-width: 768px)
and (max-width: 1099px) {
  #References section .slick-slide:nth-child(10) {
    margin: 14px 20px;
  }
  #References section .slick-slide:nth-child(11), #References section .slick-slide:nth-child(14), #References section .slick-slide:nth-child(15) {
    margin: 54px 20px;
  }
  #References section .slick-slide:nth-child(12) {
    margin: 42px 20px;
  }
  #References section .slick-slide:nth-child(16) {
    margin: 48px 20px;
  }
}

@media only screen
and (min-width: 1100px) {
  #References section .slick-slide:nth-child(13), #References section .slick-slide:nth-child(16), #References section .slick-slide:nth-child(17) {
    margin: 52px 20px;
  }
  #References section .slick-slide:nth-child(19) {
    margin: 40px 11px;
  }
  #References section .slick-slide:nth-child(7), #References section .slick-slide:nth-child(8) {
    margin: 48px 20px;
  }
  #References section .slick-slide:nth-child(12) {
    margin: 11px 20px;
  }
}

.slick-prev:before,
.slick-next:before {
  color: black;
}
